.pinButton {
  border: none;
  border-radius: 50%;
  height: 5rem;
  width: 5rem;
  font-size: 1.3rem;
  background-color: var(--trans-white);
  box-shadow: 4px 2px 32px 1px rgba(0, 0, 0, 0.3);
  color: var(--oxford-blue);
}

.pinButton:hover {
  background-color: var(--trans-black);
  color: var(--medium-sea-green);
}
